import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';


/**
 * Generated class for the ExtrophieVesicalePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-extrophie-vesicale',
  templateUrl: 'extrophie-vesicale.html',
})
export class ExtrophieVesicalePage {

  checkedItem:boolean=false;

  public Consultation = <any> [
    { title: 'Rhabilitation' , item: "Evaluation de la possibilit d'une rhabilitation prcoce (sans ranimation).", isChecked: false , count: 1},
    { title: 'Comorbidits', item: 'Evaluation des comorbidits du patient.', isChecked: false , count: 1},
    { title: 'Post-opratoire', item: 'Anticiper la priode post-opratoire : appele systmatique de la ranimation pour rserver une place (4 3416)', isChecked: false , count: 1 },
    { title : 'Epargne sanguine' , item: "Stratgie d'pargne sanguine : prescription de fer, bilan avec NFS, hmostase complte, carte de groupe. Associer un ionogramme sanguin systmatique.", isChecked: false , count: 1  },
    { title : 'Infections' ,item: 'Stratgie de contrle des infections : ECBU, dpistage BLSE par couvillon rectal et carte fongique (comportant couiville plaque vsicale, peau  la jonction de la plaque vsicale, aisselles, anus).', isChecked: false  , count: 1},
  ];

  public VPA = <any> [
    { title: 'Bilans' , item: "Vrifier que les bilans prescrits ont t raliss, vrifier les rsultats. Prescrire les RAI et la 2e dtermination de groupe.", isChecked: false , class: this.checkedItem},
    { title: 'Transfusion', item: 'Faire une commande de culots globulaires prvisionnelle.', isChecked: false , class: this.checkedItem },
    { title: 'Hydratation', item: 'Perfuser ds le dbut du jene avec du B26.', isChecked: false , class: this.checkedItem  },
     ];

  public PreOperatoire = <any> [
      { title: "Ranimation" , item:"Confirmer la place en ranimation." , isChecked: false, },
      { title: "Monitorage", item:"NIRS en place avant l'induction. TOF pour curarisation continue en IVSE - tracrium - selon le protocole habituel. " , isChecked: false, },
      { title: "Prvention", item: "Infections : Antibioprophylaxie par Cfazoline IV 30 minutes avant l'incision. Saignement : Exacyl - si ostotomie - selon le protocole habituel. NVPO : Dexamthasone selon le protocole habituel. Calculer le VPOP." , isChecked: false, },
      { title: "Dispostifs", item:"Pose de cathter veineux central en jugulaire interne droit (pas en fmoral car ostotomie). Attention au montage des lignes type ranimation. Discuter la pose d'un cathter artriel pour monitorage hmodynamique, selon le terrain. Pose d'un cathter de pridurale, utiliser en per-opratoire, valuer l'efficacit en per-opratoire." , isChecked: false, },
      ];
  
  public PerOperatoire = <any> [
    { title: "Pendant l'intervention", item:  "Surveillance des saignements en per-opratoire et HemoCue rguliers (1 par heure pendant le temps orthopdique). Seuil transfusionnel  8 g/dL. NFS de contrle en post-opratoire."
                   , isChecked: false },
    { title: "En fin d'intervention", item: "Evaluation des critres d'admission en ranimation : transfert en ranimation si transfusion massive, support catcholaminergique, impossibilit d'extubation rapide en SSPI, gestion de l'analgsie prvue comme difficile (mauvaise efficacit de la pridurale, rinjections frquentes de sufentanil en per-opratoire)."  
                    , isChecked: false },
     ];

  public PostOperatoire = <any> [
    { title: "Rhabilitation prcoce", item:  "Chaque fois que possible en l'absence de critre d'admission en ranimation. Extubation prcoce en SSPI. Contrle de l'analgsie par pridurale et paractamol. Reprise des boissons (liquide clair) ds le soir."
    , isChecked: false },
    { title : "Clinique", item:"Stabilit hmodynamique et diurse satisfaisante (> 3mL/kg/h) surveille toutes les 2 heures."},
    { title: "Biologie", item: "NFS  J1 (objectif Hb > 8 g/dL et hmostase normale). Absence de trouble ionique. Stabilit hmodynamique et diurse satisfaisante (> 3mL/kg/h)."  
     , isChecked: false },
     { title: "Dispositifs", item:"Ablation du cathter artriel avant la sortie de SSPI. Vrification de l'efficacit de l'analgsie pridurale."}
  ];


  isShownConsultation:boolean=false; 
  isShownVPA:boolean=false; 
  isShownPerOp:boolean=false; 
  isShownPostOp:boolean=false; 
  isShownPreOp:boolean=false;

  allCheckedConsultation:boolean=false; 
  allCheckedVPA:boolean=false; 
  allCheckedPreOp:boolean=false;
  allCheckedPerOp:boolean=false;
  allCheckedPostOp:boolean=false; 


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public storage: Storage,
    public ToastController: ToastController, 
    ) {
  }

  retourHome() {
    this.navCtrl.pop();
  };

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExtrophieVesicalePage');
  };

  ionViewWillEnter(){

  };

  ToggleConsultation() {
    this.isShownConsultation = !this.isShownConsultation; 
  };
  
  ToggleVPA() {
    this.isShownVPA = !this.isShownVPA;
  };

  TogglePreOp() {
    this.isShownPreOp = !this.isShownPreOp;
  };

  TogglePerOp() {
    this.isShownPerOp = !this.isShownPerOp; 
  };

  TogglePostOp() {
    this.isShownPostOp = !this.isShownPostOp;
  };

  checkCS() {
   let checkCS = this.Consultation
          .filter(item => item.isChecked === false)
          .reduce((sum, current) => sum + current.count, 0);

    if (checkCS == 0){
      this.allCheckedConsultation = true;
      const toastCS = this.ToastController.create({
        message: "Items spcifiques de consultation tous valids.",
        position: 'Bottom',
        showCloseButton: true,
        closeButtonText: "OK.",
        duration:3000, 
        });
      toastCS.present();
        }
    else {this.allCheckedConsultation = false;};
  };

checkVPA() {

let checkVPA = this.VPA
    .filter(item => item.isChecked === false)
    .reduce((sum, current) => sum + current.count, 0);

if (checkVPA == 0){
this.allCheckedVPA = true;
const toastVPA = this.ToastController.create({
  message: "Items spcifiques de VPA tous valids.",
  position: 'Bottom',
  showCloseButton: true,
  closeButtonText: "OK.",
  duration:3000, 
  });
toastVPA.present();
  }
else {this.allCheckedVPA = false;};

  };

  checkPreOperatoire(){

let checkPreOp = this.PreOperatoire
    .filter(item => item.isChecked === false)
    .reduce((sum, current) => sum + current.count, 0);

if (checkPreOp == 0){
this.allCheckedPreOp = true;
const toastPreOp = this.ToastController.create({
  message: "Items spcifiques du pr-opratoire tous valids.",
  position: 'Bottom',
  showCloseButton: true,
  closeButtonText: "OK.",
  duration:3000, 
  });
  toastPreOp.present();
  }
else {this.allCheckedPreOp = false;};
 
  };

  checkPerOperatoire() {

    let checkPerOp = this.PerOperatoire
    .filter(item => item.isChecked === false)
    .reduce((sum, current) => sum + current.count, 0);

if (checkPerOp == 0){
this.allCheckedPerOp = true;
const toastPerOp = this.ToastController.create({
  message: "Items spcifiques du per-opratoire tous valids.",
  position: 'Bottom',
  showCloseButton: true,
  closeButtonText: "OK.",
  duration:3000, 
  });
  toastPerOp.present();
  }
else {this.allCheckedPerOp = false;};

  }; 

  checkPostOperatoire() {

let checkPostOp = this.PostOperatoire
  .filter(item => item.isChecked === false)
  .reduce((sum, current) => sum + current.count, 0);

if (checkPostOp == 0){
this.allCheckedPostOp = true;
const toastPostOp = this.ToastController.create({
  message: "Items spcifiques du post-opratoire tous valids.",
  position: 'Bottom',
  showCloseButton: true,
  closeButtonText: "OK.",
  duration:3000, 
  });
  toastPostOp.present();
  }
else {this.allCheckedPostOp = false;};

  };


}



